@keyframes animated-stripes {
	from {
		transform: translateY(-3vh);
	}
	to {
		transform: translateY(0vh);
	}
}

@keyframes animated-lines {
	from {
		transform: translateY(3.75vh);
	}
	to {
		transform: translateY(0vh);
	}
}

@keyframes opacity-change1 {
	0%, 100% {
		opacity: 1;
	}
	50% {
		opacity: 0;
	}
}

@keyframes opacity-change2 {
	0%, 100%, 50% {
		opacity: 0;
	}
	25% {
		opacity: 0.5;
	}
	75% {
		opacity: 0.5;
	}
}

@keyframes opacity-change3 {
	0%, 100% {
		opacity: 0;
	}
	50% {
		opacity: 1;
	}
}

@keyframes opacity-change4 {
	0%, 100% {
		opacity: .5;
	}
	50% {
		opacity: .75;
	}
}

@keyframes opacity-change5 {
	0%, 100% {
		opacity: .2;
	}
	50% {
		opacity: .0;
	}
}

@keyframes opacity-change6 {
	0%, 100% {
		opacity: .6;
	}
	50% {
		opacity: .3;
	}
}

@keyframes hue-change1 {
	0% {
		filter: hue-rotate(0deg);
		opacity: .75;
	}
	50% {
		opacity: 1;
	}
	100% {
		filter: hue-rotate(360deg);
		opacity: .75;
	}
}

@keyframes hue-change2 {
	0% {
		filter: hue-rotate(180deg);
		opacity: .75;
	}
	50% {
		opacity: 1;
	}
	100% {
		filter: hue-rotate(540deg);
		opacity: .75;
	}
}

@keyframes hue-change3 {
	0% {
		filter: hue-rotate(0deg);
	}
	100% {
		filter: hue-rotate(90deg);
	}
}

@keyframes hue-change4 {
	0% {
		filter: hue-rotate(0deg);
	}
	100% {
		filter: hue-rotate(-50deg);
	}
}

@keyframes hue-change5 {
	0% {
		filter: hue-rotate(100deg) opacity(0.3) saturate(1) brightness(0.8);
	}
	100% {
		filter: hue-rotate(460deg) opacity(0.3) saturate(1) brightness(0.8);
	}
}

@keyframes stars-zoom {
	0% {
		opacity: 0;
		background-size: 157%;
	}
	100% {
		opacity: 1;
		background-size: 187%;
	}
}

@keyframes falling {
	0% {
		transform: translateY(0vh) translateX(10vh);
		height: 5vh;
		width: .5vh;
	}
	8% {
		height: 10vh;
		width: .1vh;
	}
	28% {
		transform: translateY(200vh) translateX(10vh);
		height: 7vh;
	}
	100% {
		transform: translateY(200vh) translateX(10vh);
	}
}

@keyframes rotation1 {
	0%, 100% {
		transform: rotate(-60deg);
	}
	25% {
		transform: rotate(-25deg);
	}
	75% {
		transform: rotate(-70deg);
	}
}

@keyframes track-left-right {
	0%, 100% {
		left: 10vh;
	}
	25% {
		left: 0vh;
	}
	50% {
		left: 42vh;
	}
}

body {
	background-color: darkgrey;
}

.poster-frame {
	height: 90vh;
	width: 58.5vh;
	background-color: #131316;
	margin: 5vh auto;
	outline: .25rem solid #555555;
	overflow: hidden;
	position: relative;
}

.logo {
	width: 100%;
	height: 25vh;
	background-image: url(../../dist/img/logo.png);
	background-repeat: no-repeat;
	position: absolute;
	background-size: 100%;
	z-index: 600;
	background-position-x: 50%;
	background-position-y: 50%;
}

.divider {
	width: 100%;
	height: 40vh;
	background-image: url(../../dist/img/divider.svg);
	background-repeat: no-repeat;
	position: absolute;
	background-size: 100%;
	z-index: 600;
	background-position-x: 50%;
	background-position-y: 69%;
	transform: scaleY(0.4) scaleX(0.7);
	filter: opacity(0.6) hue-rotate(-30deg) saturate(0.5);
}

.pattern {
	position: absolute;
	top: -24vh;
	left: 0;
	width: 100%;
	background-size: 50%;
	height: 60%;
	z-index: 4;
	background-image: url(../../dist/img/pattern.svg);
	background-repeat: repeat;
	box-shadow: inset 0vh -20vh 20vh -3vh #131316;
	animation: hue-change5 20s infinite linear;
}

.sun {
	height: 40vh;
	width: 40vh;
	background-color: purple;
	transform: translateY(25vh) translateX(23%);
	border-radius: 50%;
	overflow: hidden;
	position: absolute;
	z-index: 2;
}

.sun .sun-color-1, .sun .sun-color-2, .sun .sun-color-3 {
	height: 100%;
	width: 100%;
	position: absolute;
	top: 0;
	left: 0;
}

.sun .sun-color-1 {
	background-image: linear-gradient(#EEBAA5, #DE7D8E 70%);
	animation: opacity-change1;
	animation-duration: 10s;
	animation-iteration-count: infinite;
	animation-timing-function: linear;
}

.sun .sun-color-2 {
	background: #5583BE;
	animation: opacity-change2;
	animation-duration: 10s;
	animation-iteration-count: infinite;
	animation-timing-function: linear;
}

.sun .sun-color-3 {
	background-image: linear-gradient(#EF4700 20%, #FF9919 90%);
	animation: opacity-change3;
	animation-duration: 10s;
	animation-iteration-count: infinite;
	animation-timing-function: linear;
}

.sun .stripes {
	animation-name: animated-stripes;
	animation-duration: 2s;
	animation-iteration-count: infinite;
	animation-timing-function: linear;
	z-index: 10;
	position: relative;
}

.sun .stripes div {
	height: 3vh;
	margin: 0;
	width: 100%;
	display: flex;
	flex-direction: column;
}

.sun .stripes div div {
	height: 1.2vh;
	background-color: #131316;
}

.city-wrapper {
	position: absolute;
	z-index: 101;
	top: 20vh;
	left: 0;
	height: 100%;
	width: 100%;
}

.city-wrapper .city-buildings {
	width: 100%;
	height: 60%;
	position: absolute;
	background-image: url(../../dist/img/skyline.png);
	background-size: 151%;
	background-position-x: 51%;
	transform: scaleY(0.65);
	top: 12vh;
	background-repeat: no-repeat;
	filter: brightness(0.7);
	z-index: 10;
}

.city-wrapper .city-skyline {
	width: 100%;
	height: 57%;
	position: absolute;
	background-image: url(../../dist/img/Skyline.svg);
	background-size: 110%;
	background-position-x: 36%;
	background-position-y: 100%;
	transform: scaleY(0.85);
	top: 8vh;
	background-repeat: no-repeat;
	filter: brightness(0.5);
	z-index: 9;
}

.city-wrapper .city-building {
	width: 100%;
	height: 57%;
	position: absolute;
	background-image: url("../../dist/img/empire state building.png");
	background-size: 148%;
	background-position-x: 86%;
	background-position-y: 120%;
	transform: scaleY(0.85);
	top: 8vh;
	background-repeat: no-repeat;
	filter: brightness(0.3);
	z-index: 9;
}

.back-gradient-up {
	background: linear-gradient(to top, #ff02d5 0%, rgba(125, 185, 232, 0) 100%);
	position: absolute;
	width: 100%;
	height: 20vh;
	top: 55vh;
	opacity: 0.5;
	animation: hue-change3 20s, opacity-change6 20s;
	animation-direction: alternate;
	animation-iteration-count: infinite;
	animation-timing-function: linear;
	z-index: 2;
}

.back-gradient-down {
	background: linear-gradient(to bottom, #2500f7 0%, rgba(125, 185, 232, 0) 100%);
	position: absolute;
	width: 100%;
	height: 61vh;
	top: 0vh;
	opacity: 0.2;
	animation: opacity-change5;
	animation-direction: alternate;
	animation-iteration-count: infinite;
	animation-timing-function: linear;
	animation-duration: 10s;
	z-index: 1;
}

.front-gradient-up {
	background: linear-gradient(to top, #0ca5ff 0%, rgba(125, 185, 232, 0) 100%);
	box-shadow: 0px 12px 12px -7px #116ee0c0;
	position: absolute;
	width: 100%;
	height: 3vh;
	top: 72vh;
	opacity: 0.4;
	z-index: 111;
}

.front-gradient-down {
	background: linear-gradient(to bottom, #0285ff 0%, rgba(125, 185, 232, 0) 100%);
	position: absolute;
	width: 100%;
	height: 16vh;
	top: 75vh;
	opacity: 0.5;
	z-index: 110;
}

.grid {
	width: 100%;
	height: 90vh;
	position: absolute;
	transform-origin: 50% 50%;
	transform: perspective(9vh) rotateX(67deg) scaleX(5.6) translate3d(0vh, 9vh, -65vh);
	box-shadow: 2vh 0px 67vh 57vh rgba(255, 2, 2, 0.1);
	top: 19vh;
	animation: hue-change4 20s, opacity-change4 5s;
	animation-direction: alternate;
	animation-iteration-count: infinite;
	animation-timing-function: linear;
	animation-duration: 20s;
	opacity: 1;
}

.grid .horizontal-lines, .grid .vertical-lines {
	position: absolute;
	width: 100%;
	height: 100%;
	top: 0;
	left: 0;
	opacity: 0.75;
}

.grid .horizontal-lines {
	animation: animated-lines;
	animation-duration: .8s;
	animation-iteration-count: infinite;
	animation-timing-function: linear;
}

.grid .horizontal-lines div {
	width: 100%;
	height: 3.75vh;
}

.grid .horizontal-lines div div {
	width: 100%;
	height: .5vh;
	background-color: blue;
	box-shadow: 0px 0px 13px 2px rgba(20, 47, 184, 0.5);
}

.grid .vertical-lines {
	display: flex;
	justify-content: flex-start;
}

.grid .vertical-lines div {
	height: 100%;
	width: 14.2857143%;
}

.grid .vertical-lines div div {
	width: 0.15vh;
	height: 100%;
	background-color: blue;
	box-shadow: 0px 0px 13px 2px rgba(20, 47, 184, 0.5);
}

.grid .h-line-0, .grid .v-line-0 {
	visibility: hidden;
}

.car {
	position: absolute;
	width: 100%;
	height: 50vh;
	background-image: url(../../dist/img/car_4_v2.gif);
	background-size: 50%;
	background-repeat: no-repeat;
	background-position: center;
	z-index: 200;
	top: 50vh;
	filter: hue-rotate(55deg);
}

.headlights {
	position: absolute;
	width: 100%;
	height: 50vh;
	background-image: url(../../dist/img/headlights.svg);
	background-size: 50%;
	background-repeat: no-repeat;
	background-position: center;
	top: 50vh;
	z-index: 500;
}

.headlight {
	background: linear-gradient(to bottom, #fffcbc 0%, rgba(255, 252, 197, 0.5) 14%, rgba(255, 255, 255, 0) 100%);
	width: 10vh;
	height: 20vh;
	position: absolute;
	border-radius: 50%;
	top: 90vh;
	z-index: 300;
	opacity: .75;
	filter: blur(15px) hue-rotate(-160deg);
}

.headlight.headlight-left {
	left: 12vh;
	transform: scaleY(3) scaleX(2) rotate(15deg) perspective(14vh) skew(-6deg, -23deg);
}

.headlight.headlight-left:after {
	content: '';
	background: linear-gradient(to bottom, #fff200 0%, rgba(255, 242, 0, 0.5) 14%, rgba(255, 255, 255, 0.5) 100%);
	width: 3vh;
	height: 7vh;
	position: absolute;
	border-radius: 10%;
	top: 0vh;
	left: 3vh;
	z-index: 300;
	opacity: 0.5;
	animation: hue-change1;
	animation-duration: 5s;
	animation-iteration-count: infinite;
	animation-direction: alternate;
	animation-timing-function: linear;
}

.headlight.headlight-right {
	left: 36vh;
	transform: scaleY(3) scaleX(2) rotate(-15deg) perspective(14vh) skew(6deg, 23deg);
}

.headlight.headlight-right:after {
	content: '';
	background: linear-gradient(to bottom, #fff200 0%, rgba(255, 242, 0, 0.5) 14%, rgba(255, 255, 255, 0.5) 100%);
	width: 3vh;
	height: 7vh;
	position: absolute;
	border-radius: 10%;
	top: 0vh;
	left: 3vh;
	z-index: 300;
	opacity: .5;
	animation: hue-change2;
	animation-duration: 5s;
	animation-iteration-count: infinite;
	animation-direction: alternate;
	animation-timing-function: linear;
}

.stars {
	position: absolute;
	width: 100%;
	height: 83%;
	top: 0vh;
	left: 0;
	filter: contrast(1.5);
	background-image: url(../../dist/img/stars.png);
	background-repeat: no-repeat;
	object-fit: contain;
	background-size: 157%;
	background-position-x: 70%;
	background-position-y: 398%;
	z-index: 1;
	box-shadow: inset 0vh -15vh 17vh -3vh #131316;
	animation-iteration-count: infinite;
	animation-direction: alternate;
	animation-timing-function: linear;
	animation-duration: 20s;
}

.comet-outer {
	height: 50vh;
	top: 0;
	width: 100%;
	overflow: hidden;
}

.comet-container {
	position: relative;
	height: 150%;
	width: 20vh;
	top: -25vh;
	left: 10vh;
	z-index: 40;
	animation: rotation1 57s 4s infinite, track-left-right 15s 10s infinite;
	animation-timing-function: linear;
}

.comet-container .comet {
	position: relative;
	background-color: white;
	border-radius: 120% 120% 50% 50%;
	box-shadow: 0px 0px 12px 0px white;
	transform: translateX(10vh);
	animation: falling;
	animation-duration: 9s;
	animation-delay: 4s;
	animation-iteration-count: infinite;
	animation-timing-function: cubic-bezier(0.38, 0, 0, 1.06);
}

.comet-container .comet:after {
	content: '';
	position: absolute;
	height: 2.5vh;
	width: 0.5vh;
	border-radius: 50%;
	background-color: lightblue;
	top: 7.7vh;
	opacity: 0.5;
	box-shadow: 0px 0px 12px 1px #06cdff;
}
